*, :before, :after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

* {
  margin: 0;
  padding: 0;
  outline: 0;
  border: none;
  -webkit-text-size-adjust: 100%; }

a {
  cursor: pointer; }

a:hover {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%; }

ul, ol {
  list-style: none; }

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1;
  background: #f9f9f9; }

.wrapper {
  overflow: hidden; }

.container {
  max-width: 320px;
  padding: 0 10px;
  margin: 0 auto;
  position: relative;
  z-index: 1; }
